.slidebar-verify{
    width: 100%;
    height: 40px;
    display: inline-block;
    line-height: 40px;
    background-color: #ddd;
    position: relative;
    margin:0 auto;
    .bg{
        width:40px;
        height: 100%;
        position: absolute;
        background-color: #44b549;
    }
    .text{
        position: absolute;
        width: 100%;
        height: 100%;
        text-align: center;
        user-select: none;
    }
    .btn{
        width:40px;
        height: 40px;
        position: absolute;
        border:1px solid #ccc;
        cursor: move;
        text-align: center;
        background-color: #fff;
        user-select: none;
        color:#666;
    }
}

